@import '../../sass/index.scss';

.user-not-found {
    padding: 30px 0;

    p {
        text-align: center;

        &:nth-child(1) {
            font-size: 25px;
            font-weight: bold;
        }

        &:nth-child(2) {
            font-size: 16px;
        }

    }

    a {
        display: block;
        text-align: center;
    }
}